@import '../../style/variables.scss';
@import '../../style/container.scss';
@import '../../style/buttons.scss';

.random {
	position: relative;
	color: $white-color;
	a {
		text-decoration: none;
	}
	&__bg {
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 1;
		filter: blur(3px);
		background: transparent url('../../assets/bg_base.jpg') 20% 50% / 100%  no-repeat;
	}
	&__body {
		z-index: 2;
		height: 90vh;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		div, a {
			z-index: 2;
		}
	}
	&__text {
		max-width: 500px;
		text-align: center;
		margin: 30px;
	}
	&__title {
		font-size: 30px;
		margin-bottom: 20px;
	}
}
